import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
    {
        path: "/home",
        name: "Home",
        component: () => import("../views/IndexPage.vue"),
    },
    {
        path: encodeURI("/奥运风采馆"),
        name: "奥运风采馆",
        component: () => import("../views/奥运风采馆.vue"),
        children: [
            {
                path: encodeURI("奥运会介绍"),
                name: "奥运会介绍",
                component: () => import("@/views/奥运会介绍.vue"),
            },
            {
                path: encodeURI("各国风采展示") + "/:where",
                name: "各国风采展示",
                component: () => import("@/components/各国风采展示.vue"),
                props: true,
            },
            { path: "*", redirect: encodeURI("奥运会介绍") },
        ],
    },
    {
        path: encodeURI("/中国馆"),
        name: "中国馆",
        component: () => import("@/views/中国奥运情况.vue"),
        children: [
            {
                path: encodeURI("BMI指数报告"),
                name: "BMI指数报告",
                component: () => import("@/components/BMI相关.vue"),
            },
            {
                path: encodeURI("中国奖牌旭日图"),
                name: "中国奖牌旭日图",
                component: () => import("@/components/中国奖牌旭日图.vue"),
            },
            {
                path: encodeURI("中国奖牌柱状图"),
                name: "中国奖牌柱状图",
                component: () => import("@/components/中国奖牌柱状图.vue"),
            },
            { path: "*", redirect: encodeURI("中国奖牌旭日图") },
        ],
    },
    { path: "*", redirect: "home" },
];

const router = new VueRouter({
    routes,
});

export default router;
